import { Avatar, Button, Dialog, Typography } from "@mui/material";
import React, { useEffect, useState } from "react";
import { useAlert } from "react-alert";
import { useDispatch, useSelector } from "react-redux";
import { useParams } from "react-router-dom";
import {
  addFriend,
  getUserPosts,
  getUserProfile,
} from "../../Actions/User";
import Loader from "../Loader/Loader";
import Post from "../Post/Post";
import User from "../User/User";

const UserProfile = () => {
  const dispatch = useDispatch();
  const alert = useAlert();

  const {
    user,
    loading: userLoading,
    error: userError,
  } = useSelector((state) => state.userProfile);

  const { user: me } = useSelector((state) => state.user);
  const { loading, error, posts } = useSelector((state) => state.userPosts);
  const {
    error: friendError,
    message,
    loading: friendLoading,
  } = useSelector((state) => state.like);

  const params = useParams();
  const [friendToggle, setfriendToggle] = useState(false);
  const [friend, setfriend] = useState(false);
  const [myProfile, setMyProfile] = useState(false);

  const addfriendHandler = async () => {
    setfriendToggle(!friend);
    await dispatch(addFriend(user._id));
    dispatch(getUserProfile(params.id));
  };

  useEffect(() => {
    dispatch(getUserPosts(params.id));
    dispatch(getUserProfile(params.id));
  }, [dispatch, params.id]);

  useEffect(() => {
    if (me._id === params.id) {
      setMyProfile(true);
    }
    if (user) {
      user.friends.forEach((item) => {
        if (item._id === me._id) {
          setfriend(true);
        } else {
          setfriend(false);
        }
      });
    }
  }, [user, me._id, params.id]);

  useEffect(() => {
    if (error) {
      alert.error(error);
      dispatch({ type: "clearErrors" });
    }

    if (friendError) {
      alert.error(friendError);
      dispatch({ type: "clearErrors" });
    }

    if (userError) {
      alert.error(userError);
      dispatch({ type: "clearErrors" });
    }
    if (message) {
      alert.success(message);
      dispatch({ type: "clearMessage" });
    }
  }, [alert, error, message, friendError, userError, dispatch]);

  return loading === true || userLoading === true ? (
    <Loader />
  ) : (
    <div className="account">
      <div className="accountleft">
        {posts && posts.length > 0 ? (
          posts.map((post) => (
            <Post
              key={post._id}
              postId={post._id}
              caption={post.caption}
              postImage={post.image.url}
              likes={post.likes}
              comments={post.comments}
              ownerImage={post.owner.avatar.url}
              ownerName={post.owner.name}
              ownerId={post.owner._id}
            />
          ))
        ) : (
          <Typography variant="h6">User has not made any post</Typography>
        )}
      </div>
      <div className="accountright">
        {user && (
          <>
            <Avatar
              src={user.avatar.url}
              sx={{ height: "8vmax", width: "8vmax" }}
            />

            <Typography variant="h5">{user.name}</Typography>

            <div>
              <button onClick={() => setfriendToggle(!friendToggle)}>
                <Typography>Friends</Typography>
              </button>
              <Typography>{user.friends.length}</Typography>
            </div>


            <div>
              <Typography>Posts</Typography>
              <Typography>{user.posts.length}</Typography>
            </div>

            {myProfile ? null : (
              <Button
                variant="contained"
                style={{ background: friend ? "red" : "" }}
                onClick={addfriendHandler}
                disabled={friendLoading}
              >
                {friend ? "UnFriend" : "Add Friend"}
              </Button>
            )}
          </>
        )}
        <Dialog
          open={friendToggle}
          onClose={() => setfriendToggle(!setfriend)}
        >
          <div className="DialogBox">
            <Typography variant="h4">Friends</Typography>

            {user && user.friends.length > 0 ? (
              user.friends.map((friend) => (
                <User
                  key={friend._id}
                  userId={friend._id}
                  name={friend.name}
                  avatar={friend.avatar.url}
                />
              ))
            ) : (
              <Typography style={{ margin: "2vmax" }}>
                You have no friend
              </Typography>
            )}
          </div>
        </Dialog>

      </div>
    </div>
  );
};

export default UserProfile;
